* {
    box-sizing: border-box;
}

html, body {
  font-family: 'Roboto', sans-serif;
  margin: 0px;
}

body {
    display: flex;
    flex-direction: column;

    height: 100%vh;

    background-color: #f9f9f9;
}


header, main, footer {
    text-align: center;
}

header,.form, main {
    margin: 0 auto 70px;
    width: 90%;
    max-width: 500px;
}

header button {
    margin-top: 8px;
    padding: 8px 16px;
    border-radius: 4px;
    border: none;

    background-color: #ff4f4f;
    color: white;
    font-weight: bold;
    

}

header h1, main h2 {
    margin-bottom: 70px;
}

h1, h2 {
    color: #000000cc;
}

main h2 span {
    color: #ff4f4f;
}

.form h2 {
    text-align: right;
    color: #ffffffee;
    font-size: 18px;
}

main .donors {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
} 

section.form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 45px;
    align-items: center;

    background-color: #ff4f4f;

    padding: 16px 64px;

    border-radius: 6px;
}

form input,
form button {
    width: 100%;
    margin-bottom: 4px;

    box-sizing: border-box;

    padding: 8px 16px;

    border-radius: 4px;
    border: 1px #ffffff88 solid;
}

form input {
    background-color: transparent;
}

form input::placeholder{
    color: #ffffffaa;
}


form button {
    font-weight: bold;
}

img.logo {
    width: 120px;
    margin: 70px 0px;
}

header p {
    font-size: 14px;
}

.blood {
    color: #ff4f4f;
    font-weight: bold;
    font-size: 12px;

    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;

    
}

.blood::before {
    content: "";
    background-color: #ff4f4f11;

    width: 40px;
    height: 40px;

    border-radius: 50%;

    border-top-right-radius: 0;

    transform: rotate(-45deg);
    position: absolute;
}

.donor p {
    margin-top: 24px;
    font-size: 14px;
}

footer {
    margin-top: auto;
    padding-bottom: 16px;

    border: 1px solid #eeeeee
}
